-selected {\n  background-color: #565656;\n}\n\n.toc-code-cell-prompt {\n  flex: 0 0 27px;\n  color: var(--jp-cell-prompt-not-active-font-color);\n  opacity: var(--jp-cell-prompt-not-active-opacity);\n  font-family: var(--jp-cell-prompt-font-family);\n  padding: var(--jp-code-padding);\n  padding-right: 0px;\n  padding-left: 0px;\n  letter-spacing: var(--jp-cell-prompt-letter-spacing);\n  line-height: var(--jp-code-line-height);\n  font-size: 8px;\n  border: var(--jp-border-width) solid transparent;\n  text-align: left;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\n.toc-toolbar {\n  position: relative;\n  width: 100%;\n  margin: 0px;\n  user-select: none;\n  border-bottom: var(--jp-border-width) solid var(--jp-border-color2);\n  height: 36px;\n  display: flex;\n  align-items: center;\n}\n\n.toc-code-cell-div {\n  display: inline-flex;\n  width: 100%;\n}\n\n.toc-entry-holder {\n  width: 100%;\n  display: flex;\n  flex-direction: row;\n  padding: 0px;\n  margin: 0px;\n  overflow: visible;\n}\n\n.toc-Collapser-child {\n  display: block;\n  width: 100%;\n  box-sizing: border-box;\n  position: absolute;\n  top: 0px;\n  bottom: 0px;\n}\n\n.toc-entry-holder:hover .toc-Collapser-child {\n  box-shadow: var(--jp-elevation-z2);\n  background: var(--jp-brand-color1);\n  opacity: var(--jp-cell-collapser-not-active-hover-opacity);\n}\n\n.toc-active-cell .toc-Collapser-child {\n  background: var(--jp-brand-color1);\n}\n\n.toc-active-cell .toc-Collapser-child:hover {\n  background: var(--jp-brand-color0);\n  opacity: 1;\n}\n\n.toc-active-cell:hover .toc-Collapser-child {\n  background: var(--jp-brand-color0);\n  opacity: 1;\n}\n\n.toc-Collapser-child {\n  background: transparent;\n}\n\n.toc-Collapser-child:hover {\n  box-shadow: var(--jp-elevation-z2);\n  background: var(--jp-brand-color1);\n  opacity: var(--jp-cell-collapser-not-active-hover-opa